const xddshsdgget =(value) => {
    let key = ""
    for(let i = 0; i< tempkey.length; i++){
        key += tempkey[i].charCodeAt(0).toString(10)
    }
    let retval = "",j=0, flag = false; value = JSON.stringify(value);
    for (let i = 0; i < value.length; i++){
        let temp = value[i]; let ascii = value[i].charCodeAt(0); 
        if(temp == '"') { j = 0; flag = !flag }
        else if (flag && (ascii>=48 && ascii<=126)){
                let ref = parseInt(key[ j% key.length])
                let first = true
                while(first || ascii >= 91 && ascii<= 96){
                    first = false; 
                    ascii -= ref ; if(ascii < 48) ascii = 126 + (ascii-48)
                }
                j += 1; temp = String.fromCharCode(ascii)
        }
        retval += temp
    }
    retval = retval.replace(/[A-Z][A-Z]n/g,"\\n")
    return JSON.parse(retval);
}


